*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.app{
  background: #f7f7f7;
  height: 100vh;
}

.flex{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container{
  background-color: #fff;
  width: 70vw;
  text-align: center;
  border-radius: 1rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  padding:10px;
}

.ul-container{
  flex-direction: column;
  list-style: none;
}

.li-item{
  background: #e2e2e2;
  font-size: 1rem;
  padding: .5rem;
  border-radius: 1rem;
  width:200px;
  margin:5px;
  transition: background .3s
}

.li-item:hover{
  cursor:pointer;
  background:#e7e7e7;
}